.margined {
  margin: 1em 0;
}

.horizontal {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.status {
  height : 2em;
  width : 100%;
}

.enabled {
  opacity: 1;
  transition-property: opacity;
  transition-duration: 1s;
}

.disabled {
  pointer-events: none;
  opacity: 0.3;
  transition-property: opacity;
  transition-duration: 1s;
}